@import url(https://fonts.googleapis.com/css?family=Ubuntu&display=swap);
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background: rgb(165, 167, 148);
	background: -webkit-gradient(linear, left top, right top, from(rgba(165, 167, 148, 1)), color-stop(40%, rgba(92, 93, 97, 1)));
	background: linear-gradient(90deg, rgba(165, 167, 148, 1) 0%, rgba(92, 93, 97, 1) 40%);
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

button {
	cursor: pointer;
}

body {
	font-family: "Ubuntu", sans-serif;
}

.center {
	display: flex;
	justify-content: center;
}
.particles {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: -1;
}
.App {
	text-align: center;
}

.Tilt {
	background: rgb(211, 218, 205);
	background: -webkit-gradient(linear, left top, right top, from(rgba(211, 218, 205, 1)), color-stop(75%, rgba(152, 153, 150, 1)));
	background: linear-gradient(90deg, rgba(211, 218, 205, 1) 0%, rgba(152, 153, 150, 1) 75%);
}

